@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif;box-sizing:border-box}body{background-color:#737373}.container{max-width:1280px;margin:0 auto}img,object,video,embed{max-width:100%}

.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/air-quality-react/static/media/hero-bg.c5f577b9.jpg);background-size:cover;padding:5rem 0 11.125rem}.hero-partners{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-align-items:center;align-items:center}.hero-partners-item{margin-left:5rem}.hero-partners-item:first-child{margin-left:0}.hero-navigation{margin-top:4.45rem}.hero-navigation-item{text-decoration:none;font-weight:normal;font-size:1.2em;line-height:1.4rem;color:#fff;margin-left:4.55rem}.hero-navigation-item:first-child{margin-left:0}.hero-title{max-width:664px;font-weight:normal;font-size:3.55em;line-height:4.2rem;color:#fff;margin:6.75rem 0 7rem}.hero-link{display:inline-block;padding:2.2rem 9.5rem;text-decoration:none;font-weight:normal;font-size:2em;line-height:2.3rem;color:#fff;background:#B78A3F}@media (max-width: 1280px){.hero{padding:5rem 2rem 11.125rem}}@media (max-width: 768px){.hero-navigation-item{font-size:1em;line-height:1.2rem;margin-left:2rem}.hero-navigation-item:first-child{margin-left:0}.hero-title{font-size:3em;line-height:3.8rem;color:#fff;margin:4.75rem 0 6rem}}@media (max-width: 768px) and (max-width: 576px){.hero{padding:3rem 2rem 9rem}.hero-partners{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.hero-partners-item{margin-bottom:1rem;margin-left:0}.hero-partners-item:first-child{margin-left:0}.hero-navigation{margin-top:4.45rem}.hero-navigation-item{display:-webkit-flex;display:flex;font-size:1.2em;line-height:1.4rem;margin-bottom:.5rem;margin-left:0}.hero-navigation-item:first-child{margin-left:0}.hero-title{font-size:2.2em;line-height:3rem;margin:4rem 0 6rem}.hero-link{padding:1.5rem 6rem;font-size:2em;line-height:2.3rem}}@media (max-width: 414px){.hero{padding:1rem 1rem 9rem}.hero-partners{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between