.facts{box-sizing:border-box;position:relative;padding:100px 60px 50px}.facts-title{font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#fff;margin-bottom:30px}.facts-slide-desc{margin-top:40px;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#fff}.facts>.swiper-button-prev,.facts .swiper-button-next{color:#B78A3F}.facts>.swiper-button-prev{left:10px}.facts>.swiper-button-next{right:10px}.facts>.swiper-button-disabled{opacity:0}.facts-pagination{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin-top:66px}.facts-pagination-bullet{display:inline-block;width:24px;height:24px;border-radius:50%;margin:0 20px;background-color:transparent;border:2px solid #B78A3F;opacity:1;transition:0.3s background-color}.facts-pagination>.swiper-pagination-bullet-active{background-color:#B78A3F}@media (max-width: 414px){.facts-slide-desc{margin-top:20px;font-weight:normal;font-size:20px;line-height:27px;text-align:center;color:#fff}.facts-pagination-bullet{width:14px;height:14px;margin:0 10px}}

.air-q{padding:50px 0}.air-q-title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#fff;margin-bottom:30px}.air-q-map-state{position:absolute;top:0;left:0;margin:12px;background-color:#404040;color:#ffffff;padding:6px;font-weight:bold}.air-q-map{height:560px;margin:0 0 42px;position:relative}.air-q-map-state{position:absolute;top:0;left:0;margin:12px;background-color:#404040;color:#ffffff;padding:6px;font-weight:bold}.air-q-text{font-weight:normal;font-size:23px;line-height:27px;color:#fff}.mapboxgl-control-container{display:none}@media (max-width: 414px){.air-q-map{margin:0 5px 10px}.air-q-text{font-size:18px;line-height:22px;margin:0 10px}}

.sectors{padding:50px 0}.sectors-title{font-weight:normal;font-size:35px;line-height:41px;color:#fff;text-align:center;margin-bottom:30px}.sectors-subtitle{font-weight:normal;font-size:23px;line-height:27px;color:#FFFFFF;margin:42px 0 30px}.sectors-list{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}.sectors-list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-bottom:30px}.sectors-list-item-name{width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222;background:rgba(255,255,255,0.83)}.sectors-list-item-tooltip{position:absolute;left:100%;width:440px;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#DADADA;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);z-index:1;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip-switched{left:auto;left:initial;right:100%;-webkit-clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);padding:16px 75px 16px 16px}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@media (max-width: 414px){.sectors-subtitle{font-size:18px;line-height:22px;margin:0 20px 20px}.sectors-list{-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;padding:0 20px}.sectors-list-item{position:relative;width:100%}.sectors-list-item-tooltip{position:absolute;top:57%;left:0;right:0;width:100%;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#DADADA;padding:16px;-webkit-clip-path:none;clip-path:none;z-index:1;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip-switched{left:0;right:0;-webkit-clip-path:none;clip-path:none;padding:16px}}

.footer{border-top:1px solid #FFFFFF}.footer-partners{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-top:2rem;margin-bottom:5rem}.footer-partners-item{margin-right:120px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.footer-nav{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:88px}.footer-nav-link{font-weight:normal;font-size:17px;line-height:20px;text-decoration:none;color:#fff;margin-bottom:20px}.footer-nav-link:last-child{margin-bottom:0}.footer-nav-text{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#fff}.footer-text{font-weight:300;font-size:14px;line-height:14px;color:#fff;margin-bottom:14px}.text-yellow{color:#EDE852}@media (max-width: 740px){.footer-partners{padding:0 20px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-top:1rem;margin-bottom:7rem}.footer-partners-item{max-width:20%;margin-right:15px}.footer-partners-item:last-child{margin-right:0}}@media (max-width: 414px){.footer-text-wrap{margin:0 10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between}.footer-nav{margin-bottom:50px;-webkit-align-items:center;align-items:center;margin-bottom:88px}.footer-nav-text{margin:10px 0 50px;font-size:14px;line-height:19px;text-align:center;width:100%}.footer-nav-link{margin-bottom:10px}.footer-text{text-align:center}}

